@use '../abstracts/' as *;

.contact {
  display: flex;
  gap: 1rem;
  flex-flow: column nowrap;
  align-items: center;
  form {
    flex: 1 1 100%;
    width: 75%;
  }
}

.user,
.email,
.message-input {
  width: 100%;
  border-radius: 5px;
} 

.message-input {
  min-height: 200px;
}